.dishCard {
    width: 100%;
    margin: 0 auto;
    position: relative;
}

.anchor {
    position: absolute;
    top: -150px;
    left: 0;
    visibility: hidden;
}

.dishTitle {
    color: white;
    text-align: center;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 2rem;
    margin-top: 2rem;
    margin-bottom: 2.5rem;
    padding: 0 1rem;
    line-height: 1.3;
}

.dishList {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(4, 1fr); /* 4 карточки в ряд для десктопа */
    gap: 1.5rem;
    justify-items: center;
    margin-bottom: 4rem;
}

/* Планшеты */
@media (max-width: 1024px) {
    .anchor {
        top: -160px;
    }

    .dishTitle {
        font-size: 1.75rem;
        margin-bottom: 2rem;
    }

    .dishList {
        grid-template-columns: repeat(3, 1fr); /* 3 карточки в ряд для планшетов */
        gap: 1.25rem;
        margin-bottom: 3rem;
    }
}

/* Мобильные */
@media (max-width: 768px) {
    .anchor {
        top: -195px;
    }

    .dishTitle {
        font-size: 1.5rem;
        margin-bottom: 1.5rem;
    }

    .dishList {
        grid-template-columns: repeat(2, 1fr); /* 2 карточки в ряд */
        gap: 1rem;
        margin-bottom: 2.5rem;
    }
}

/* Очень маленькие экраны */
@media (max-width: 480px) {
    .anchor {
        top: -180px;
    }

    .dishTitle {
        font-size: 1.3rem;
        margin-bottom: 1rem;
        padding: 0 0.5rem;
    }

    .dishList {
        grid-template-columns: repeat(2, 1fr); /* 2 карточки в ряд */
        gap: 0.75rem;
        margin-bottom: 2rem;
    }
}

/* Очень широкие экраны */
@media (min-width: 1440px) {
    .anchor {
        top: -155px;
    }

    .dishList {
        grid-template-columns: repeat(4, 1fr); /* 4 карточки в ряд */
        gap: 2rem;
    }
}
