.middle {
    width: 100%;
    max-width: 75rem;
    margin: 0 auto;
    padding: 0 1rem;
    box-sizing: border-box;
    padding-top: 10rem;
}

@media (max-width: 1024px) {
    .middle {
        padding-top: 10rem;
    }
}

@media (max-width: 768px) {
    .middle {
        padding-top: 10rem; /* Отступ для мобильных */
        padding-left: 0.75rem;
        padding-right: 0.75rem;
    }
}

@media (max-width: 480px) {
    .middle {
        padding-top: 9rem; /* Отступ для маленьких экранов */
        padding-left: 0.5rem;
        padding-right: 0.5rem;
    }
}
