.sideMenu {
    position: fixed;
    top: 0;
    left: 0;
    width: 0;
    height: 0;
    background-color: rgb(237, 154, 193);
    overflow: hidden;
    padding-top: 4rem;
    box-sizing: border-box;
    transition: width 0.3s ease, height 0.3s ease;
    z-index: 1101;
    display: none;
    flex-direction: column;
    align-items: center;
}

.sideMenu.open {
    height: 100vh;
    width: 100vw;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.closeButton {
    display: none;
}

.sideMenu.open .closeButton {
    display: block;
    position: fixed;
    top: 1rem;
    right: 1rem;
    font-size: 2rem;
    background: transparent;
    border: none;
    color: white;
    cursor: pointer;
    z-index: 1110;
    width: 2.5rem;
    height: 2.5rem;
    line-height: 1;
}

.appLinksMobile {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    flex-direction: column;
    gap: 1rem;
    list-style: none;
    padding: 0;
    margin: 0;
}

.appLinksMobile li {
    margin: 0.5rem 1rem;
    cursor: pointer;
    padding: 1rem 2rem;
    border-radius: 8px;
    font-size: 1.3rem;
    user-select: none;
    transition: background-color 0.3s ease;
    text-align: start;
    background-color: rgba(255, 255, 255, 0.1);
}

.appLinksMobile li:hover {
    background-color: rgba(255, 255, 255, 0.2);
}

.appLinksMobile li a {
    color: white;
    font-weight: bold;
    text-decoration: none;
    display: block;
}

.appLinks {
    width: 100%;
    max-width: 75rem;
    margin: 0 auto;
    display: flex;
    flex-wrap: nowrap; /* В одну строку */
    justify-content: center;
    list-style: none;
    padding: 0.4rem 1rem;
    background-color: transparent;
    box-sizing: border-box;
    overflow-x: auto; /* На случай если не влезет */
    overflow-y: hidden;
}

/* Скрываем скроллбар */
.appLinks::-webkit-scrollbar {
    display: none;
}

.appLinks {
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.appLinks li {
    margin: 0 0.25rem;
    cursor: pointer;
    padding: 0.35rem 0.7rem;
    border-radius: 5px;
    font-size: 0.85rem;
    user-select: none;
    transition: background-color 0.3s ease;
    text-align: center;
    white-space: nowrap; /* Не переносить текст */
    flex-shrink: 0; /* Не сжимать элементы */
}

.appLinks li a.name {
    color: white;
    font-weight: normal;
    text-decoration: none;
}

.appLinks li:hover {
    background-color: rgba(255, 255, 255, 0.2);
}

/* Для широких экранов */
@media (min-width: 1280px) {
    .appLinks {
        padding: 0.5rem 1rem;
    }

    .appLinks li {
        font-size: 0.9rem;
        padding: 0.4rem 0.8rem;
        margin: 0 0.3rem;
    }
}

/* Планшеты */
@media (max-width: 1024px) {
    .appLinks li {
        font-size: 0.8rem;
        padding: 0.3rem 0.6rem;
        margin: 0 0.2rem;
    }
}

/* Мобильные */
@media (max-width: 768px) {
    .sideMenu {
        display: block;
    }

    .appLinks {
        display: none;
    }

    .appLinksMobile li {
        font-size: 1.1rem;
        padding: 0.75rem 1.5rem;
    }
}
