.app {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100vw;
    margin: 0;
    padding: 0;
    background-color: rgb(2, 53, 36);
    z-index: 100;
    overflow: visible;
    box-sizing: border-box;
}

.topContact {
    width: 100%;
    max-width: 75rem;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 0.7rem;
    color: white;
    text-shadow: 0 0 4px rgba(0,0,0,0.7);
    box-sizing: border-box;
    user-select: text;
    padding: 0.4rem 1rem;
    flex-wrap: wrap;
    gap: 0.4rem;
}

.maps,
.telephoneNumber {
    cursor: pointer;
    text-decoration: none;
    color: white;
}

.maps:hover,
.telephoneNumber:hover {
    text-decoration: underline;
}

.telephoneNumber,
.orderTime,
.maps {
    margin: 0 0.25rem;
    padding: 0.1rem;
    font-weight: normal;
}

.topRow {
    width: 100%;
    max-width: 75rem;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    padding: 0.5rem 1rem;
    box-sizing: border-box;
    min-height: 50px;
}

.menuButton {
    background: none;
    border: none;
    cursor: pointer;
    padding: 0;
    width: 40px;
    height: 40px;
    display: none;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 6px;
    z-index: 10;
    flex-shrink: 0;
}

.menuButton span {
    display: block;
    width: 28px;
    height: 3px;
    background-color: white;
    border-radius: 2px;
    transition: all 0.3s ease;
}

.menuButton:hover span {
    background-color: rgba(255, 255, 255, 0.8);
}

.appLogo {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.appLogo img {
    width: 5.5rem;
    height: auto;
    display: block;
}

.bucket {
    background-color: transparent;
    border: none;
    cursor: pointer;
    width: 38px;
    height: 38px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    transition: transform 0.2s ease;
    flex-shrink: 0;
    z-index: 10;
}

.bucket:hover {
    transform: scale(1.1);
}

.bucket img {
    width: 100%;
    height: auto;
}

/* Планшеты */
@media (max-width: 1024px) {
    .topContact {
        font-size: 0.68rem;
        padding: 0.4rem 1rem;
    }

    .topRow {
        padding: 0.6rem 1rem;
        min-height: 55px;
    }

    .appLogo img {
        width: 5.5rem;
    }

    .bucket {
        width: 40px;
        height: 40px;
    }
}

/* Мобильные устройства */
@media (max-width: 768px) {
    .topContact {
        font-size: 0.7rem;
        padding: 0.5rem 1rem;
        gap: 0.3rem;
    }

    .telephoneNumber,
    .orderTime,
    .maps {
        margin: 0 0.2rem;
        font-size: 0.7rem;
    }

    .topRow {
        padding: 0.75rem 1rem;
        min-height: 70px;
    }

    .menuButton {
        display: flex;
        width: 35px;
        height: 35px;
    }

    .menuButton span {
        width: 25px;
        height: 2.5px;
        gap: 5px;
    }

    .appLogo {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
    }

    .appLogo img {
        width: 5.5rem;
    }

    .bucket {
        width: 40px;
        height: 40px;
    }
}

/* Очень маленькие экраны */
@media (max-width: 480px) {
    .topContact {
        font-size: 0.65rem;
        padding: 0.5rem 0.75rem;
        text-align: center;
    }

    .telephoneNumber,
    .orderTime,
    .maps {
        margin: 0 0.15rem;
        font-size: 0.65rem;
    }

    .topRow {
        padding: 0.5rem 0.75rem;
        min-height: 60px;
    }

    .menuButton {
        width: 30px;
        height: 30px;
    }

    .menuButton span {
        width: 22px;
        height: 2px;
    }

    .appLogo img {
        width: 4.5rem;
    }

    .bucket {
        width: 35px;
        height: 35px;
    }
}

/* Для широких экранов */
@media (min-width: 1280px) {
    .topContact {
        font-size: 0.75rem;
        padding: 0.45rem 1rem;
    }

    .topRow {
        min-height: 55px;
        padding: 0.6rem 1rem;
    }

    .appLogo img {
        width: 6rem;
    }

    .bucket {
        width: 42px;
        height: 42px;
    }
}
