.dish {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin-top: 4rem;
    padding: 0;
    box-sizing: border-box;
}

.notification {
    position: fixed;
    top: 150px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1000;
    width: 90%;
    max-width: 500px;
}

.wrapper {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding: 1rem 1rem 2rem; /* Уменьшен верхний отступ с 2rem до 1rem */
    margin: 0 auto;
    width: 100%;
    max-width: 75rem;
    box-sizing: border-box;
}

.container {
    display: flex;
    gap: 3rem;
    width: 100%;
    flex-wrap: nowrap;
    align-items: flex-start;
}

.imageWrapper {
    width: 400px;
    flex-shrink: 0;
}

.image {
    width: 100%;
    height: auto;
    aspect-ratio: 1/1;
    object-fit: cover;
    border: 4px solid white;
    border-radius: 12px;
    box-sizing: border-box;
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.2);
}

.info {
    flex: 1;
    padding: 0;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    min-width: 0;
}

.section {
    color: rgba(255, 255, 255, 0.7);
    font-size: 0.9rem;
    font-weight: 600;
    letter-spacing: 0.1em;
    margin-bottom: 0.5rem;
}

.title {
    color: white;
    font-size: 2.5rem;
    font-weight: 700;
    line-height: 1.2;
    margin: 0 0 1rem 0;
    overflow-wrap: break-word;
}

.description {
    font-size: 1.1rem;
    color: rgba(255, 255, 255, 0.9);
    line-height: 1.6;
    margin-bottom: 1.5rem;
    overflow-wrap: break-word;
}

/* Планшеты */
@media (max-width: 1024px) {
    .wrapper {
        padding: 0.75rem 1rem 1.5rem;
    }

    .container {
        gap: 2rem;
    }

    .imageWrapper {
        width: 320px;
    }

    .title {
        font-size: 2.2rem;
    }

    .description {
        font-size: 1rem;
    }

    .notification {
        top: 160px;
    }
}

/* Мобильные */
@media (max-width: 768px) {
    .wrapper {
        padding: 0.5rem 0.75rem 1rem; /* Уменьшен верхний отступ */
    }

    .container {
        flex-direction: column;
        gap: 1.5rem;
        align-items: center;
    }

    .imageWrapper {
        width: 100%;
        max-width: 280px;
    }

    .image {
        border: 3px solid white;
    }

    .info {
        width: 100%;
        padding: 0 0.5rem;
    }

    .title {
        font-size: 1.8rem;
        text-align: center;
    }

    .section {
        text-align: center;
        font-size: 0.85rem;
    }

    .description {
        font-size: 0.95rem;
        text-align: center;
    }

    .notification {
        top: 210px;
        width: 95%;
    }
}

/* Очень маленькие экраны */
@media (max-width: 480px) {
    .wrapper {
        padding: 0.5rem 0.5rem 0.75rem; /* Еще меньше отступ */
    }

    .container {
        gap: 1rem;
    }

    .imageWrapper {
        max-width: 240px;
    }

    .image {
        border: 2px solid white;
        border-radius: 10px;
    }

    .title {
        font-size: 1.6rem;
    }

    .section {
        font-size: 0.8rem;
    }

    .description {
        font-size: 0.9rem;
        line-height: 1.5;
    }

    .notification {
        top: 195px;
    }
}
