.notification {
    margin: 0 auto;
    padding: 1rem 1.5rem;
    max-width: 500px;
    background-color: white;
    color: #333;
    border-radius: 12px;
    box-shadow: 0 6px 20px rgba(0, 0, 0, 0.2);
    text-align: center;
    cursor: pointer;
    user-select: none;
    font-size: 1.1rem;
    font-weight: 500;
    animation: slideDown 0.3s ease;
}

@keyframes slideDown {
    from {
        opacity: 0;
        transform: translateY(-20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@media (max-width: 768px) {
    .notification {
        padding: 0.85rem 1.25rem;
        font-size: 1rem;
    }
}

@media (max-width: 480px) {
    .notification {
        padding: 0.75rem 1rem;
        font-size: 0.95rem;
    }
}
