.price {
    display: block;
    margin: 1rem 0;
    color: white;
}

.cena {
    color: white;
    font-size: 2rem;
    font-weight: 700;
    letter-spacing: 0.02em;
}

.zl {
    text-transform: uppercase;
    font-size: 1.5rem;
    font-weight: 400;
    margin-left: 0.25rem;
}

@media (max-width: 768px) {
    .price {
        text-align: center;
    }

    .cena {
        font-size: 1.75rem;
    }

    .zl {
        font-size: 1.25rem;
    }
}

@media (max-width: 480px) {
    .cena {
        font-size: 1.5rem;
    }

    .zl {
        font-size: 1.1rem;
    }
}
