* {
    box-sizing: border-box;
}

html, body {
    width: 100%;
    max-width: 100vw;
    overflow-x: hidden;
    margin: 0;
    padding: 0;
}

body {
    min-width: 320px;
    background-color: rgb(2, 53, 36);
    -webkit-text-size-adjust: 100%;
}

#root {
    width: 100%;
    overflow-x: hidden;
}

img {
    max-width: 100%;
    height: auto;
}
